body {
  background-color: var(--bg);
}

header {
  top: 0;
  left: 0;
  right: 0;
  padding: 0 30px;
  position: fixed;
  display: flex;
  align-items: center;
  gap: 20px;
  background-color: var(--bg);
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.25);
}

header h1 {
  font-size: 3rem;
}

header img {
  height: var(--header-height);
  width: auto;
}

#basketball {
  transform: rotate(30deg);
}

#nba { 
  margin-left: auto;
}
